.progress-bar-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: #e0e0e03c;
  z-index: 1000;
  display: flex;
  align-items: center;
}

.progress-bar {
  height: 100%;
  background: #6200ea;
  transition: width 0.1s;
}

.progress-keypoint {
  position: absolute;
  top: 0;
  width:8px;
  height: 100%;
  background: #ff00ff;
  transition: left 0.1s;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 4px;
}

.progress-keypoint:hover {
  scale: 4;
}
